	// 
	caculateTimeZone() {

		var d = new Date();
		var local = d.getTime();
		var offset = d.getTimezoneOffset() * 60000
		//
		var localUtc = new Date().getTimezoneOffset() / 60;
		return localUtc;

	},
	//UTC0 () dateString
	toChangeUTCFormat(dateString) {
		let utc0;
		let date;
		if (dateString == '' || dateString == null || dateString == undefined) {
			date = new Date();
		} else {
			date = new Date(dateString)
		}
		let localtime = date.getTime();
		//let localOffSet = date.getTimezoneOffset() * 60000
		//utc0 = localtime + localOffSet
		return localtime;
	},
	//)
	 MillisecondToDate(msd) {
		let time = parseFloat(msd) / 1000;
		if (null != time && "" != time) {
			if (time > 60 && time < 60 * 60) {
				time = parseInt(time / 60.0) + "" + parseInt((parseFloat(time / 60.0) -
					parseInt(time / 60.0)) * 60) + "";
			} else if (time >= 60 * 60 && time < 60 * 60 * 24) {
				time = parseInt(time / 3600.0) + "" + parseInt((parseFloat(time / 3600.0) -
						parseInt(time / 3600.0)) * 60) + "" +
					parseInt((parseFloat((parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60) -
						parseInt((parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60)) * 60) + "";
			} else {
				time = parseInt(time) + "";
			}
		}
		return time;
	},
	//
	getLocalDateNoFormat(utcString) {
		let utc = new Date(utcString).getTime()
		let timeZone = this.caculateTimeZone().toString()
		let symbol = timeZone.substring(0, 1)
		let offset = parseFloat(timeZone.substring(1))
		let localUTC;
		if (symbol == "+") {
			localUTC = utc - (3600000 * offset)
		} else {
			localUTC = utc + (3600000 * offset)
		}
		let localDate = new Date(localUTC)
		return localDate
	},
	// utc utc  fmt  YYYY-mm-dd HH:MM:SS  MmM m
	showLocalDate(utcString, fmt) {
		let localDate = new Date(utcString)
		return this.dataFormat(fmt, localDate)
	},
