		watch: {
			// 
			$route: {
				handler() {
					if (this.$route.params.civilCategoryIndex) {
						this.civilCategoryIndex = this.$route.params.civilCategoryIndex;
					} else {
						this.civilCategoryIndex = -1;
					}
				},
				//param
				deep: true,
			},
			//
			isScroll(newVal, oldVal) {
				this.scroll = newVal;
			},
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				let categoryList = this.$store.state.categoryInfo;
				this.secondCategoryList = categoryList.find((item) => item.categoryName == "").children;
				this.params.categoryUuid = categoryList.find((item) => item.categoryName == '').uuid
			}
			if (this.$route.params.civilCategoryIndex) {
				this.civilCategoryIndex = this.$route.params.civilCategoryIndex;
			}
		},
