<script>
	import {
		newsList,
	} from '../../../apis/openapi.js';
	import pagination from '@/components/pagination';
	import UTCUtil from '../../../../static/js/UTCUtil.js';
	export default {
		components: {
			pagination,
		},
		props: {
			//
			civilCategoryIndex: {
				type: String,
			},
			//
			scroll: {
				type: Number,
			},
			// uuid
			secCategoryUuid:{
				type:String,
				default:''
			}
		},
		data() {
			return {
				disputeIndex: 0, //
				totalCount: 100, //
				params: {
					page: 1,
					rows: 6,
					categoryUuid: ''
				},
				newsAllList: [],

			}
		},
		watch: {
			scroll: {
				handler(val, oldVal) {
					if (val <= 1350) {
						this.disputeIndex = 0
					} else if (val <= 2350) {
						this.disputeIndex = 1
					} else if (val <= 2880) {
						this.disputeIndex = 2
					} else if (val <= 3850) {
						this.disputeIndex = 3
					} else if (val <= 4400) {
						this.disputeIndex = 4
					}
				},
				deep: true
			}
		},

		mounted() {

		},
		methods: {
			//
			newsList() {
				let params = {
					...this.params,
					secCategoryUuid:this.secCategoryUuid
				}
				newsList(params).then(res => {
					if (res.data.success == 'true') {
						this.newsAllList = res.data.data.data
						this.totalCount = res.data.data.total
						this.newsAllList.forEach(item => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, 'YYYY-mm');
								item.day = UTCUtil.showLocalDate(item.publishTime, 'dd');
							}
						})
					}
				});
			},
			//
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList()
			},
			chooseDispute(index) {
				//this.disputeIndex = index
				this.goAuchor('#contract' + index)
			},
			goAuchor(id) {
				document.querySelector(id).scrollIntoView({
					behavior: "smooth"
				});
			},

		}
	}
</script>
