: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, , ; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: ;\"></span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 32px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, , ; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: ;\"><span style=\"padding: 0px; margin: 0px;\"></span><span style=\"padding: 0px; margin: 0px;\"></span></span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 32px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, , ; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: ;\"></span><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: ;\"></span><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: ;\"><span style=\"padding: 0px; margin: 0px;\"></span><span style=\"padding: 0px; margin: 0px;\"></span></span></p><p><br/></p>"
				}, //
				params: {
					page: 1,
					rows: 6,
					categoryUuid:''
				},

			}
		},
		watch: {
			$route: { // query
				handler() {
					if (this.$route.query.newsUuid) {
						this.newsDetail ={}
						this.showDetails = false;
						this.toDetails(this.$route.query.newsUuid)
					}else{
						this.params.page = 1;
						this.showDetails = true;
						this.newsList()
					}
					//param
				},
				deep: true,
			}
		},
		mounted() {
			if (this.$route.params.categoryUuid){
				this.params.categoryUuid = this.$route.params.categoryUuid
			}
			if (this.$route.params.secCategoryUuid){
				this.params.secCategoryUuid = this.$route.params.secCategoryUuid
			}
			if (this.$route.query.newsUuid) {
				this.newsDetail ={}
				this.showDetails = false;
				this.toDetails(this.$route.query.newsUuid)
			}
			this.newsList()
			this.getCategoryList()

		},
		methods: {
			//
			getCategoryList() {
				let params = {
					page: 1,
					rows: 9999,
					categoryName: '',
					parentUuid: '0',
					categoryState: ''
				}
				getCategoryList(params).then(res => {
					if (res.data.success == 'true') {
						this.categoryList = res.data.data.data
					}
				})
			},
			//
			newsList() {
				newsList(this.params).then(res => {
					if (res.data.success == 'true') {
						this.newsAllList = res.data.data.data
						this.totalCount = res.data.data.total
						this.newsAllList.forEach(item => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, 'YYYY/mm');
								item.day = UTCUtil.showLocalDate(item.publishTime, 'dd');
							}
						})
						this.isShowNoData = true
					}
				});
			},
			chooseCategory(uuid){
				this.params.page = 1;
				this.params.categoryUuid =uuid
				this.showDetails = true;
				this.newsList()
			},
			//()
			toNewsCenter(uuid) {
				this.$router.push({
					path: '/news-center-app',
					query: {
						newsUuid: uuid
					}
				});
			},
			toDetails(uuid){
				getNewsDetail({uuid:uuid}).then(res => {
					if (res.data.success == 'true') {
						this.newsDetail = res.data.data
						if (this.newsDetail.publishTime) {
							this.newsDetail.publishTime = UTCUtil.showLocalDate(this.newsDetail.publishTime, 'YYYY-mm-dd');
						}
						this.showDetails = false;
						document.body.scrollTop = 0; // chrome
						document.documentElement.scrollTop = 0; // firefox
					}
				});
			},
			//
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList()
				document.body.scrollTop = 0; // chrome
				document.documentElement.scrollTop = 0; // firefox
			},
			//
			toggleNavigation(url) {
				this.$router.push({
					path: url,
					query: {
					}
				});
				//this.$forceUpdate()
			},

		}
	}
